  created() {
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.user = user;
        this.$store.commit('sendToken', this.user.email)
        
        formData.set('current_user', this.user.email);
        formData.append('fun', 'user');
        // trzeba dodać w manifeście taką linię, żeby axios post działał
        //<application
        // ...
        // android:usesCleartextTraffic="true"
        axios.post("http://192.3.81.66/assets/php/app/index.php", formData)
        .then(res => {
          this.$refs.weightGraph.chart.reflow()
          this.$refs.tempGraph.chart.reflow()
          this.$refs.humiGraph.chart.reflow()
          this.$refs.soundGraph.chart.reflow()

          this.user_info = res.data;
          this.hives = [];
          for (let i = 1; i <= res.data[2]; i++){
            this.hives.push(i);
            formDataArr[i - 1] = new FormData();
            formDataArr[i - 1].set('token', this.user_info[1] + i);
            formDataArr[i - 1].append('fun', 'fill');
            this.weightData[i - 1] = [];
            this.tempData[i - 1] = [];
            this.humiData[i - 1] = [];
            this.soundData[i - 1] = [];
          }

          // poiberanie danych
