          .then(res => {
            this.weightData[0] = res.data[6].map((el, index) => [Date.parse(el + " " + res.data[7][index]), res.data[3].map(Number)[index]]);
            this.tempData[0] = res.data[6].map((el, index) => [Date.parse(el + " " + res.data[7][index]), res.data[0].map(Number)[index]]);
            this.humiData[0] = res.data[6].map((el, index) => [Date.parse(el + " " + res.data[7][index]), res.data[1].map(Number)[index]]);
            this.soundData[0] = res.data[6].map((el, index) => [Date.parse(el + " " + res.data[7][index]), res.data[2].map(Number)[index]]);

            this.weightData[0] = this.weightData[0].filter( value => !Number.isNaN(value[0]));
            this.tempData[0] = this.tempData[0].filter( value => !Number.isNaN(value[0]));
            this.humiData[0] = this.humiData[0].filter( value => !Number.isNaN(value[0]));
            this.soundData[0] = this.soundData[0].filter( value => !Number.isNaN(value[0]));

            this.w_array[0] = [+(71 * parseFloat(res.data[4][0]) - 129).toFixed()];
            if (this.w_array[0] > 100){
              this.w = 100;
              this.battery_var = "100%";
            } else {
              this.w = this.w_array[0];
              this.battery_var = this.w_array[0] + "%";
            }

            this.$refs.weightGraph.chart.addSeries({
              name: 'Waga',
              color: "#f9db1a",
              data: [],
              boostThreshold: 1000,
              cropThreshold: 3000,
            });

            this.$refs.tempGraph.chart.addSeries({
