              name: 'Dźwięk',
              color: "#f9db1a",
              data: [],
              boostThreshold: 1000,
              cropThreshold: 3000
            });

            this.$refs.weightGraph.chart.series[0].setData(this.weightData[0]);
            this.$refs.tempGraph.chart.series[0].setData(this.tempData[0]);
            this.$refs.humiGraph.chart.series[0].setData(this.humiData[0]);
            this.$refs.soundGraph.chart.series[0].setData(this.soundData[0]);

            this.changeUnit()

          })
          .catch(error => {
            console.log(error)
