    //       // this.weightOptions.series.addPoint([Date.parse(res.data[6] + " " + res.data[7]), res.data[3]], true, true)
    //       // this.tempOptions.series.addPoint([Date.parse(res.data[6] + " " + res.data[7]), res.data[0]], true, true)
    //       // this.humiOptions.series.addPoint([Date.parse(res.data[6] + " " + res.data[7]), res.data[1]], true, true)

    //       this.w = [+(71 * parseFloat(res.data[4]) - 129).toFixed()];

