        boostThreshold: 1000,
        cropThreshold: 3000
      });

      this.$refs.soundGraph.chart.addSeries({
        name: 'Dźwięk',
        color: "#f9db1a",
        data: [],
        boostThreshold: 1000,
        cropThreshold: 3000
      });

      this.$refs.weightGraph.chart.showLoading("Wczytywanie danych...");
      this.$refs.tempGraph.chart.showLoading("Wczytywanie danych...");
      this.$refs.humiGraph.chart.showLoading("Wczytywanie danych...");
      this.$refs.soundGraph.chart.showLoading("Wczytywanie danych...");

      if (this.weightData[this.selectedOption - 1].length === 0){
        console.log("case 1")
        axios.post("http://192.3.81.66/assets/php/app/index.php", formDataArr[this.selectedOption - 1])
        .then(res => {
          this.weightData[this.selectedOption - 1] = res.data[6].map((el, index) => [Date.parse(el + " " + res.data[7][index]), res.data[3].map(Number)[index]]);
          this.tempData[this.selectedOption - 1] = res.data[6].map((el, index) => [Date.parse(el + " " + res.data[7][index]), res.data[0].map(Number)[index]]);
          this.humiData[this.selectedOption - 1] = res.data[6].map((el, index) => [Date.parse(el + " " + res.data[7][index]), res.data[1].map(Number)[index]]);
          this.soundData[this.selectedOption - 1] = res.data[6].map((el, index) => [Date.parse(el + " " + res.data[7][index]), res.data[2].map(Number)[index]]);

          this.weightData[this.selectedOption - 1] = this.weightData[this.selectedOption - 1].filter( value => !Number.isNaN(value[0]));
          this.tempData[this.selectedOption - 1] = this.tempData[this.selectedOption - 1].filter( value => !Number.isNaN(value[0]));
          this.humiData[this.selectedOption - 1] = this.humiData[this.selectedOption - 1].filter( value => !Number.isNaN(value[0]));
          this.soundData[this.selectedOption - 1] = this.soundData[this.selectedOption - 1].filter( value => !Number.isNaN(value[0]));

          this.w_array[this.selectedOption - 1] = [+(71 * parseFloat(res.data[4][0]) - 129).toFixed()];
          if (this.w_array[this.selectedOption - 1] > 100){
            this.w = 100;
            this.battery_var = "100%";
          } else {
            this.w = this.w_array[this.selectedOption - 1];
            this.battery_var = this.w_array[this.selectedOption - 1] + "%";
          }
          console.log(this.selectedOption)

          this.$refs.weightGraph.chart.hideLoading();
          this.$refs.tempGraph.chart.hideLoading();
          this.$refs.humiGraph.chart.hideLoading();
          this.$refs.soundGraph.chart.hideLoading();

          this.$refs.weightGraph.chart.series[0].setData(this.weightData[this.selectedOption - 1]);
          this.$refs.tempGraph.chart.series[0].setData(this.tempData[this.selectedOption - 1]);
          this.$refs.humiGraph.chart.series[0].setData(this.humiData[this.selectedOption - 1]);
          this.$refs.soundGraph.chart.series[0].setData(this.soundData[this.selectedOption - 1]);

          this.changeUnit();
        })
        .catch(error => {
          console.log(error)
            // Manage errors if found any
        })
      } else {
        console.log("case 2")
        this.$refs.weightGraph.chart.hideLoading();
        this.$refs.tempGraph.chart.hideLoading();
        this.$refs.humiGraph.chart.hideLoading();
        this.$refs.soundGraph.chart.hideLoading();

        this.$refs.weightGraph.chart.series[0].setData(this.weightData[this.selectedOption - 1]);
        this.$refs.tempGraph.chart.series[0].setData(this.tempData[this.selectedOption - 1]);
        this.$refs.humiGraph.chart.series[0].setData(this.humiData[this.selectedOption - 1]);
        this.$refs.soundGraph.chart.series[0].setData(this.soundData[this.selectedOption - 1]);

        this.changeUnit()

        if (this.w_array[this.selectedOption - 1] > 100){
            this.w = 100;
            this.battery_var = "100%";
        } else {
          this.w = this.w_array[this.selectedOption - 1];
          this.battery_var = this.w_array[this.selectedOption - 1] + "%";
        }
      }
    },
    doRefresh(event) {
      console.log('Begin async operation');
      for (let i = 0; i < this.weightData.length; i++){
        this.weightData[i] = [];
        this.tempData[i] = [];
        this.humiData[i] = [];
        this.soundData[i] = [];
      }
      this.changeHive();
      setTimeout(() => {
        console.log('Async operation has ended');
        event.target.complete();
      }, 2000);
    }
  },
});
</script>
